/* 
1. import(): xxx.js
2. import(), 
    ()
*/
const Home = () => import('@/pages/Home')
const Search = () => import('@/pages/Search')
const Detail = () => import('@/pages/Detail')

import Register from '../pages/Register'
// import Login from '../pages/Login'
import Login from '@/pages/Login'
import AddCartSuccess from '@/pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'

import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
import Center from '@/pages/Center'
import MyOrder from '@/pages/Center/MyOrder'
import GroupBuy from '@/pages/Center/GroupBuy'

import stote from '@/store'


export default [
  {
    path: '/',
    component: Home
  },
  {
    name: 'search', // params, push(), name
    path: '/search/:keyword?',  // :params
    component: Search,
    // props: (route) => ({ query: route.query.q }) 
    // props
    props: route => ({keyword3: route.params.keyword, keyword4: route.query.keyword2})
  },
  {
    path: '/register',
    component: Register,
    meta: {
      isHideFooter: true
    }
  },
  {
    path: '/login',
    component: Login,
    meta: {
      isHideFooter: true
    },
    /* : ,  */
   /*  beforeEnter: (to, from, next) => { 
      // , 
      const token = stote.state.user.userInfo.token
      if (token) {
        next('/')
      } else { // 
        next()
      }
    } */
  },

  {
    name: 'detail',  // name
    path: '/detail/:id', // name, 
    component: Detail
  },
  {
    path: '/addcartsuccess',
    component: AddCartSuccess,
    // skuNumsessionStorageskuInfo, 
    beforeEnter: (to, from, next) => { 
      // queryskuNum
      const skuNum = to.query.skuNum
      // sessionStorageskuInfo
      const skuInfo = JSON.parse(window.sessionStorage.getItem('SKU_INFO_KEY'))
      // 2
      if (skuNum && skuInfo instanceof Object) {
        next()
      } else { // , 
        next('/shopcart')
      }
    }
  },
  {
    path: '/shopcart',
    component: ShopCart
  },

  {
    path: '/trade',
    component: Trade,
    beforeEnter: (to, from, next) => { 
      // 
      if (from.path==='/shopcart') {
        next()
      } else { // 
        next('/shopcart')
      }
    }
  },
  {
    path: '/pay',
    component: Pay,
    beforeEnter: (to, from, next) => { 
      // trade
      if (from.path==='/trade') {
        next()
      } else { // trade
        next('/trade')
      }
    }
  },
  {
    path: '/paysuccess',
    component: PaySuccess,
    beforeEnter: (to, from, next) => { 
      // pay
      if (from.path==='/pay') {
        next()
      } else { // pay
        next('/pay')
      }
    }
  },
  {
    path: '/center',
    component: Center,
    children: [
      {
        path: '/center/myorder',
        component: MyOrder
      },
      {
        path: 'groupbuy',
        component: GroupBuy
      },
      // 
      {
        path: '',
        redirect: '/center/myorder'
      }
    ]
  },

  {
    path: '/communication',
    component: () => import('@/pages/Communication/Communication'),
    children: [
      {
        path: 'event',
        component: () => import('@/pages/Communication/EventTest/EventTest'),
        meta: {
          isHideFooter: true
        },
      },
      {
        path: 'model',
        component: () => import('@/pages/Communication/ModelTest/ModelTest'),
        meta: {
          isHideFooter: true
        },
      },
      {
        path: 'sync',
        component: () => import('@/pages/Communication/SyncTest/SyncTest'),
        meta: {
          isHideFooter: true
        },
      },
      {
        path: 'attrs-listeners',
        component: () => import('@/pages/Communication/AttrsListenersTest/AttrsListenersTest'),
        meta: {
          isHideFooter: true
        },
      },
      {
        path: 'children-parent',
        component: () => import('@/pages/Communication/ChildrenParentTest/ChildrenParentTest'),
        meta: {
          isHideFooter: true
        },
      },
      {
        path: 'scope-slot',
        component: () => import('@/pages/Communication/ScopeSlotTest/ScopeSlotTest'),
        meta: {
          isHideFooter: true
        },
      }
    ],
  },

  {
    path: '/scoped',
    component: () => import('@/pages/Scoped/Test1'),
    meta: {
      isHideFooter: true
    }
  },
]