/* 
vuex
*/
import {reqShopCart, reqAddToCart, reqCheckCartItem, reqDeleteCartItem} from '@/api'

const state = {
  cartList: [], // 
}

const mutations = {
  /* 
  
  */
  RECEIVE_CART_LIST (state, cartList) {
    state.cartList = cartList
  }
}

const actions = {
  /* 
  action
  */
  async getCartList ({commit}) {
    const result = await reqShopCart()
    if (result.code===200) {
      const cartList = result.data
      commit('RECEIVE_CART_LIST', cartList)
    }
  },

  /* 
  action
  */
  async addToCart ({commit}, {skuId, skuNum, callback}) {
    const result = await reqAddToCart(skuId, skuNum)
    if (result.code===200) {
      // 
      callback()  // 
    } else {
      // 
      callback('')
    }
  },

  /* 
  action
  */
  async addToCart2 ({commit}, {skuId, skuNum}) {
    const result = await reqAddToCart(skuId, skuNum)
    // , 
    if (result.code!==200) {
      // 
      throw new Error('')  // actionpromise, reasonerror
    }
  },

  /* 
  action
  */
  async addToCart3 ({commit}, {skuId, skuNum}) {
    const result = await reqAddToCart(skuId, skuNum)
    // , 
    if (result.code!==200) {
      // 
      return ''   // actionpromise, valueerrorMsg
    } else {  // else
      return undefined   // actionpromise, valueundefined
    }
  },

  /* 
  /
  skuId: ID
  isChecked: , '0', '1'
  */
  async checkCartItem ({commit}, {skuId, isChecked}) {
    // , ajax
    const result = await reqCheckCartItem(skuId, isChecked)
    // , error
    if (result.code!==200) {
      throw new Error(result.message || '')
    }
  },

  /* 
  /
  checked: 
  1). checked
  2). itemcheckCartItem()  ===> dispatch()
  3). context:
      {
        state,      //  `store.state`
        getters,    //  `store.getters`
        commit,     //  `store.commit`
        dispatch,   //  `store.dispatch`
      }
  4). , , action, 
      const promise = Promise.all([p1, p2, p3])
  */
  async checkAllCartItems ({commit, state, dispatch}, checked) {
    // isChecked
    const isChecked = checked ? '1' : '0'
    let promises = []

    // 
    state.cartList.forEach(item => {
      // 
      if (item.isChecked!=isChecked) {
        // checkCartItem, promise
        const promise = dispatch('checkCartItem', {skuId: item.skuId, isChecked})
        // 
        promises.push(promise)
      }
    })

    // ? 
    // promise(dispatch, )
    return Promise.all(promises) 
  },

  /* 
  action
  */
  async deleteCartItem (context, skuId) {
    const result = await reqDeleteCartItem(skuId)
    if (result.code!==200) { // 
      throw new Error('')   // asyncpromise
    }
  },

  /* 
  action
  */
  async deleteCheckedCartItems ({state, dispatch}) {

    // deleteCartItem
    const promises = state.cartList.reduce((pre, item) => {
      if (item.isChecked===1) {
        pre.push(dispatch('deleteCartItem', item.skuId))
      }
      return pre
    }, [])

    return Promise.all(promises)
  }
}

const getters = {
  /* 
  
  */
  totalCount (state) {
    /* 
    let total = 0
    state.cartList.forEach(item => {
      total += item.skuNum
    })
    return total 
    */
    return state.cartList.reduce((preTotal, item, index) => preTotal + (item.isChecked===1?item.skuNum:0) , 0) 
  },

  /* 
  
  */
  totalPrice (state) {
    return state.cartList.reduce((pre, item, index) => pre + item.skuNum*item.cartPrice , 0) 
  },


  /* 
  
  */
  isCheckAll (state) {
    // true, false
    return state.cartList.length>0 && state.cartList.every((item, index) => item.isChecked===1)
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
