<template>
  <header class="header">
    <!--  -->
    <div class="top">
      <div class="container">
        <div class="loginList">
          <p></p>
          <p v-if="userInfo.token">
            <span>{{userInfo.name}}</span> &nbsp;&nbsp;
            <a href="javascript:" @click="logout"></a>
          </p>
          <p v-else>
            <span></span>
            <!-- <router-link to="/login"></router-link> -->
            <router-link :to="{path: '/login'}"></router-link>
            <router-link to="/register" class="register"></router-link>
          </p>
        </div>
        <div class="typeList">
          <router-link to="/center"></router-link>
          <router-link to="/shopcart"></router-link>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
          <a href="###"></a>
        </div>
      </div>
    </div>
    <!-- -->
    <div class="bottom">
      <h1 class="logoArea">
        <router-link to="/">
          <img src="./images/logo.png" alt="">
        </router-link>
      </h1>

      <div class="searchArea">
        <form action="/xxx" class="searchForm" @submit.prevent="search">
          <input type="text" id="autocomplete" class="input-error input-xxlarge"  v-model="keyword"/>
          <!-- <button class="sui-btn btn-xlarge btn-danger" @click.prevent="search"></button> -->
          <button class="sui-btn btn-xlarge btn-danger"></button>
        </form>
      </div>
    </div>
  </header>
</template>

<script>
  export default {
    name: 'Header',

    computed: {
      userInfo () {
        return this.$store.state.user.userInfo
      }
    },

    data () {
      return {
        keyword: ''
      }
    },

    mounted () {
      // Header: , 
      this.$bus.$on('removeKeyword', () => {
        this.keyword = ''
      })
    },

    methods: {
      /* 
      
      */
      logout () {
        if (confirm('?')) {
          this.$store.dispatch('logout').then(() => {
            this.$router.push('/login')
          }).catch(error => {
            alert(error.message)
          })
        }
      },

      search () {
        const {keyword} = this
        // /
        // push('')
        // this.$router.push(`/search/${keyword}?keyword2=${keyword.toUpperCase()}`)
        
        // push({}): 
        const location = {
          name: 'search'
        }
        // keyword, params
        if (keyword) {
          location.params = {keyword}
        }

        // categoryName/cateory1Id/cateory2Id/cateory3Id, 
        // : , 
        location.query = this.$route.query

        // this.$router.push(location)  // 

        // vue-router3.1.0(promise)
        /* this.$router.push(location, () => {
          console.log('')
        }) */
       
        // vue-router, promise
        /* this.$router.push(location).catch(() => {
          // console.log('')
        }) */

        // search, push, replace
        // if (this.$route.name!=='search') {
        if (this.$route.path.indexOf('/search')!==0) {  // /search/xxx
          this.$router.push(location)
        } else {
          this.$router.replace(location)
        }
        
        // this.$router.push(location, ()=> {})
        // this.$router.replace(location, ()=> {})
      }
    }
  }
</script>

<style lang="less" scoped>
  .header {
    &>.top {
      background-color: #eaeaea;
      height: 30px;
      line-height: 30px;

      .container {
        width: 1200px;
        margin: 0 auto;
        overflow: hidden;

        .loginList {
          float: left;

          p {
            float: left;
            margin-right: 10px;

            .register {
              border-left: 1px solid #b3aeae;
              padding: 0 5px;
              margin-left: 5px;
            }
          }
        }

        .typeList {
          float: right;

          a {
            padding: 0 10px;

            &+a {
              border-left: 1px solid #b3aeae;
            }
          }

        }

      }
    }

    &>.bottom {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .logoArea {
        float: left;

        .logo {
          img {
            width: 175px;
            margin: 25px 45px;
          }
        }
      }

      .searchArea {
        float: right;
        margin-top: 35px;

        .searchForm {
          overflow: hidden;

          input {
            box-sizing: border-box;
            width: 490px;
            height: 32px;
            padding: 0px 4px;
            border: 2px solid #ea4a36;
            float: left;

            &:focus {
              outline: none;
            }
          }

          button {
            height: 32px;
            width: 68px;
            background-color: #ea4a36;
            border: none;
            color: #fff;
            float: left;
            cursor: pointer;

            &:focus {
              outline: none;
            }
          }
        }
      }
    }
  }
</style>