<template>
  <div class="pagination">
    <!-- 1 -->
    <button :disabled="myCurrentPage===1" @click="setCurrentPage(myCurrentPage-1)"></button>
    <!-- start1 -->
    <button v-if="startEnd.start>1" @click="setCurrentPage(1)">1</button>
    <!-- start2 -->
    <button v-if="startEnd.start>2" disabled></button>

    <!-- startend -->
    <!-- 
      v-forv-if? 
      v-for
      v-for, v-if
    -->
    <button v-for="item in startEnd.end" v-if="item>=startEnd.start" 
      :class="{active: item===myCurrentPage}" @click="setCurrentPage(item)">{{item}}</button>
    
    <!-- end1 -->
    <button v-if="startEnd.end<totalPages-1" disabled></button>
    <!-- end -->
    <button v-if="startEnd.end<totalPages" @click="setCurrentPage(totalPages)">{{totalPages}}</button>
    <!--  -->
    <button :disabled="myCurrentPage===totalPages" @click="setCurrentPage(myCurrentPage+1)"></button>
    
    <button disabled style="margin-left: 30px"> 60 </button>
  </div>
</template>

<script>
  export default {
    name: "Pagination",

    props: {
      // currentPage: 
      currentPage: {
        type: Number,
        default: 1
      },
      // pageSize: 
      pageSize: {
        type: Number,
        default: 10
      },
      // total: 
      total: {
        type: Number,
        default: 0
      },
      // showPageNo:  ()
      showPageNo: {
        type: Number,
        default: 5
      },
    },

    data () {
      return {
        // myCurrentPage: 
        myCurrentPage: this.currentPage  // 
      }
    },

    watch: {
      // 
      currentPage (value) {
        // 
        this.myCurrentPage = value
      }
    },

    computed: {
      /* 
      totalPages: 
				: total / pageSize   19 / 2  10
				: Math.ceil(total/pageSize)
      */
      totalPages () {
        // : total / pageSize   19 / 2  10
        const {total, pageSize} = this

        // , 
        return Math.ceil(total/pageSize)
      },

      /* 
      start/end: :  {start, end}
        : myCurrentPage / showPageNo / totalPages

      
        {start: 2, end: 5} // : 
        [2, 5] // : 
      */
      startEnd () {
        let start, end

        // 
        const {myCurrentPage, showPageNo, totalPages} = this

        // start
        /* 
        myCurrentPage, showPageNo, totalPages
            4              5           8         23[4]56
        */
        start = myCurrentPage - Math.floor(showPageNo/2) // 4 - 2

        // start1, 1
        /* 
        myCurrentPage, showPageNo, totalPages
            2              5           8         1[2]345
        start 0, 1
        */
        if (start<1) {
          start = 1
        }

        // end
        // startendshowPageNo
        /* 
        myCurrentPage, showPageNo, totalPages
            4              5           8         23[4]56
        */
        end = start + showPageNo -1  // 2 + 5 -1

        // endtotalPages, totalPages
        /* 
        myCurrentPage, showPageNo, totalPages
            7              5           8        456[7]8 

          : start5, end9
          : start4, end8

        */
        if (end>totalPages) {
          end = totalPages
          // start: startendshowPageNo
          start = end - showPageNo + 1  // 8 - 5 + 1

          /* 
          myCurrentPage, showPageNo, totalPages
              2              5          3        1[2]3
            end3 , start-1 , 1
          */
          if (start<1) {
            start = 1
          }
        }

        // startend
        return {start, end}
      }
    },

    methods: {
      /* 
      
      */
      setCurrentPage (page) {
        // , 
        if (page==this.myCurrentPage) return

        // 
        this.myCurrentPage= page

        //   ==> ? /props
        this.$emit('currentChange', page)
      }
    }


  }
</script>

<style lang="less" scoped>
  .pagination {
    button {
      margin: 0 5px;
      background-color: #f4f4f5;
      color: #606266;
      outline: none;
      border-radius: 2px;
      padding: 0 4px;
      vertical-align: top;
      display: inline-block;
      font-size: 13px;
      min-width: 35.5px;
      height: 28px;
      line-height: 28px;
      cursor: pointer;
      box-sizing: border-box;
      text-align: center;
      border: 0;

      &[disabled] {
        color: #c0c4cc;
        cursor: not-allowed;
      }

      &.active {
        cursor: not-allowed;
        background-color: #409eff;
        color: #fff;
      }
    }
  }
</style>