/* 
vuex
*/
import {reqCategoryList, reqBanners, reqFloors, reqRecommends} from '@/api'

export default {
  state: {
    categoryList: [], // 
    banners: [], // 
    floors: [], // 
    recommends: [], // 
  },
  mutations: {

    /* 
    mutation
    */
    RECEIVE_RECOMMENDS (state, recommends) {
      state.recommends = recommends
    },
    /* 
    mutation
    */
    RECEIVE_BANNERS (state, banners) {
      state.banners = banners
    },

    /* 
    mutation
    */
    RECEIVE_FLOORS (state, floors) {
      state.floors = floors
    },
    
    /* 
    mutation
    */
    RECEIVE_CATEGORY_LIST (state, categoryList) {
      state.categoryList = categoryList
    } 
  }, // mutations

  actions: {

    /* 
    action
    */
    async getRecommends ({commit}) {
      const result = await reqRecommends()
      if (result.code===200) {
        const recommends = result.data
        commit('RECEIVE_RECOMMENDS', recommends)
      }
    },

    /* 
    action
    */
    async getBanners ({commit}) {
      const result = await reqBanners()
      if (result.code===200) {
        const banners = result.data
        commit('RECEIVE_BANNERS', banners)
      }
    },

    /* 
    action
    */
    async getFloors ({commit}) {
      const result = await reqFloors()
      if (result.code===200) {
        const floors = result.data
        commit('RECEIVE_FLOORS', floors)
      }
    },

    /* 
    action
    */
    async getCategoryList ({commit}) {
      // ajax
      const result = await reqCategoryList()
      console.log('result', result)
      // , , mutation
      if (result.code===200) {
        const categoryList = result.data.filter((item, index) => index<15)
        commit('RECEIVE_CATEGORY_LIST', categoryList)
      }
    }

  }, // actions
  
  getters: {
    test4 (state) { // statestate

    }
  } // getters
}