/* 
axios(axiosXHRajax)     
1. 
    const intance = axios.create({baseURL, timeout})  // intanceajax
    instance
2.
    : : NProgress.start()
    : : NProgress.done()
3.response,response.data
    : return response.data
4.,
    : , errorpromise
*/
import axios from 'axios'
import NProgress from 'nprogress'

/* 1. */
// instanceaxiosajax
const instance = axios.create({
  baseURL: '/mock',  // path
  // baseURL: 'http://182.92.128.115/api',
  timeout: 15000 // 
})

// 
// axios.interceptors.request.use(config => { // 
instance.interceptors.request.use(config => { // 
  /* 2.1: NProgress.start() */
  NProgress.start()

  return config
})

// 
instance.interceptors.response.use(
  response => { // 
    /* 2.2:  */
    NProgress.done()

    // return response
    /* 3.response,response.data */
    return response.data
  },

  error => { // 
    /* 2.1:  */
    NProgress.done()

    /* 4., */
    // 
    alert(error.message || '')

    // error promise
    // throw error
    return Promise.reject(error)

    // return error  // 
  }
)



// instance, axios
export default instance
