<template>
  <div class="cart">
    <h4></h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1"></div>
        <div class="cart-th2"></div>
        <div class="cart-th3"></div>
        <div class="cart-th4"></div>
        <div class="cart-th5"></div>
        <div class="cart-th6"></div>
      </div>
      <div class="cart-body">

        <ul class="cart-list" v-for="item in cartList" :key="item.id">
          <li class="cart-list-con1">
            <!-- isChecked: 0:, 1:  -->
            <input type="checkbox" name="chk_list" :checked="item.isChecked===1" 
              @change="checkCartItem(item)">
          </li>
          <li class="cart-list-con2">
            <img :src="item.imgUrl">
            <div class="item-msg">{{item.skuName}}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{item.cartPrice}}</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)" class="mins" @click="changeItemCount(item, -1)">-</a>
            <!-- 
              vueDOMinput
                input: 
                change: 
            -->
              <!-- 
                3:
                  
                   CTRL + C 
                   
                @input="validInput": 
                  , , 
                @keyup="validInput" 
                  , , 

                : -0a011a0110  0110110
              -->
            <input autocomplete="off" type="text" class="itxt" :value="item.skuNum"
              @keyup="validInput" 
              @change="changeItemCount(item, $event.target.value - item.skuNum, $event)">
            <a href="javascript:void(0)" class="plus" @click="changeItemCount(item, 1)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{item.cartPrice * item.skuNum}}</span>
          </li>
          <li class="cart-list-con7">
            <a href="javascript:" class="sindelet" @click="deleteCartItem(item)"></a>
            <br>
            <a href="#none"></a>
          </li>
        </ul>

      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" v-model="checkAll">
        <span></span>
      </div>
      <div class="option">
        <a href="javascript:" @click="deleteCheckedCartItems"></a>
        <a href="#none"></a>
        <a href="#none"></a>
      </div>
      <div class="money-box">
        <div class="chosed">
          <span>{{totalCount}}</span></div>
        <div class="sumprice">
          <em> </em>
          <i class="summoney">{{totalPrice}}</i>
        </div>
        <div class="sumbtn">
          <router-link to="/trade" class="sum-btn" ></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapGetters } from 'vuex'
  export default {
    name: 'ShopCart',

    computed: {
      ...mapState({
        cartList: state => state.shopCart.cartList
      }),
      ...mapGetters(['totalCount', 'totalPrice', 'isCheckAll']),

      // gettersetter
      checkAll: {
        get () {
          return this.isCheckAll
        },
        // 
        async set (value) { // true/false
          try {
            // checkAllCartItems action  ===> 
            await this.$store.dispatch('checkAllCartItems', value)
            // , 
            this.$store.dispatch('getCartList')
          } catch (error) {
            // , 
            alert(error.message)
          }
        }
      }
    },

    mounted () {
      this.$store.dispatch('getCartList')
    },

    methods: {
      /* 
      
      */
      validInput (event) {
        console.log('validInput()-----')
        // (0+)
        const input = event.target
        // n0n
        //  /^0+|\D+0*/g :  1+0  1+0+0
        /* 
          \D  
          + >=1
          * >=0
          | 
          g 

          : -0a011a0110   11110
        */
        input.value = input.value.replace(/^0+|\D+0*/g,'')
      },

      /* 
      /
      */
      async checkCartItem (item) {
        // 
        const skuId = item.skuId
        const isChecked = item.isChecked===1 ? '0' : '1'
        try {
          // checkCartItem action  ===> 
          await this.$store.dispatch('checkCartItem', {skuId, isChecked})
          // , 
          this.$store.dispatch('getCartList')
        } catch (error) {
          // , 
          alert(error.message)
        }
      },

      /* 
      
        0 , 
      */
      async changeItemCount (item, numChange, event) {

        // , 
        const num = item.skuNum + numChange
        if (num>0) {
          // 
          const skuId = item.skuId
          const skuNum = numChange
          try {
            // action addToCart2  ==> 
            await this.$store.dispatch('addToCart2', {skuId, skuNum})
            // , 
            this.$store.dispatch('getCartList')
          } catch (error) {
            // , 
            alert(error.message)
          }
        } else {
          // ,  (0)
          if (event) {
            event.target.value = item.skuNum
          }
        }
      },

      /* 
      
      */
      async deleteCartItem (item) {
        if (window.confirm(`${item.skuName}?`)) {
          try {
            // action
            await this.$store.dispatch('deleteCartItem', item.skuId)
            // 
            this.$store.dispatch('getCartList')
          } catch (error) { // 
            alert(error.message)
          }
        }
      },

      /* 
      
      */
      async deleteCheckedCartItems () {
        if (window.confirm(`?`)) {
          try {
            // action
            await this.$store.dispatch('deleteCheckedCartItems')
            // 
            this.$store.dispatch('getCartList')
          } catch (error) { // 
            alert(error.message)
          }
        }
      }
    }

  }
</script>

<style lang="less" scoped>
  .cart {
    width: 1200px;
    margin: 0 auto;

    h4 {
      margin: 9px 0;
      font-size: 14px;
      line-height: 21px;
    }

    .cart-main {
      .cart-th {
        background: #f5f5f5;
        border: 1px solid #ddd;
        padding: 10px;
        overflow: hidden;

        &>div {
          float: left;
        }

        .cart-th1 {
          width: 5%;

          input {
            vertical-align: middle;
          }

          span {
            vertical-align: middle;
          }
        }

        .cart-th2 {
          width: 35%;
        }

        .cart-th3,
        .cart-th4,
        .cart-th5,
        .cart-th6 {
          width: 15%;

        }
      }

      .cart-body {
        margin: 15px 0;
        border: 1px solid #ddd;

        .cart-list {
          padding: 10px;
          border-bottom: 1px solid #ddd;
          overflow: hidden;

          &>li {
            float: left;
          }

          .cart-list-con1 {
            width: 5%;
          }

          .cart-list-con2 {
            width: 35%;

            img {
              width: 82px;
              height: 82px;
              float: left;
            }

            .item-msg {
              float: left;
              width: 150px;
              margin: 0 10px;
              line-height: 18px;
            }
          }

          .cart-list-con4 {
            width: 15%;

          }

          .cart-list-con5 {
            width: 15%;

            .mins {
              border: 1px solid #ddd;
              border-right: 0;
              float: left;
              color: #666;
              width: 6px;
              text-align: center;
              padding: 8px;
            }

            input {
              border: 1px solid #ddd;
              width: 40px;
              height: 33px;
              float: left;
              text-align: center;
              font-size: 14px;
            }

            .plus {
              border: 1px solid #ddd;
              border-left: 0;
              float: left;
              color: #666;
              width: 6px;
              text-align: center;
              padding: 8px;
            }
          }

          .cart-list-con6 {
            width: 15%;

            .sum {
              font-size: 16px;
            }
          }

          .cart-list-con7 {
            width: 15%;

            a {
              color: #666;
            }
          }
        }
      }
    }

    .cart-tool {
      overflow: hidden;
      border: 1px solid #ddd;

      .select-all {
        padding: 10px;
        overflow: hidden;
        float: left;

        span {
          vertical-align: middle;
        }

        input {
          vertical-align: middle;
        }
      }

      .option {
        padding: 10px;
        overflow: hidden;
        float: left;

        a {
          float: left;
          padding: 0 10px;
          color: #666;
        }
      }

      .money-box {
        float: right;

        .chosed {
          line-height: 26px;
          float: left;
          padding: 0 10px;
        }

        .sumprice {
          width: 200px;
          line-height: 22px;
          float: left;
          padding: 0 10px;

          .summoney {
            color: #c81623;
            font-size: 16px;
          }
        }

        .sumbtn {
          float: right;

          a {
            display: block;
            position: relative;
            width: 96px;
            height: 52px;
            line-height: 52px;
            color: #fff;
            text-align: center;
            font-size: 18px;
            font-family: "Microsoft YaHei";
            background: #e1251b;
            overflow: hidden;
          }
        }
      }
    }
  }
</style>