<template>
  <div class="order-content">
    <div class="title">
      <h3></h3>
    </div>
    <div class="chosetype">
      <table>
        <thead>
          <tr>
            <th width="29%"></th>
            <th width="31%"></th>
            <th width="13%"></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
      </table>
    </div>
    <div class="orders">

      <table class="order-item" v-for="order in orders" :key="order.id">
        <thead>
          <tr>
            <th colspan="5">
              <span class="ordertitle">{{order.createTime}}{{order.outTradeNo}} 
                <span class="pull-right delete">
                  <img src="./images/delete.png">
                </span>
              </span>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in order.orderDetailList" :key="item.id">
            <td width="60%">
              <div class="typographic">
                <img style="width: 100px; height: 100px;" :src="item.imgUrl">
                <a href="#" class="block-text">{{item.skuName}}</a>
                <span>x{{item.skuNum}}</span>
              </div>
            </td>
            <!-- 
              : <temlate><template>
                    , ()
              ? 
            -->
            <template v-if="index===0">
              <td :rowspan="order.orderDetailList.length" width="8%" class="center">{{order.consignee}}</td>
              <td :rowspan="order.orderDetailList.length" width="13%" class="center">
                <ul class="unstyled">
                  <li>{{order.totalAmount}}</li>
                  <li>{{order.paymentWay==='ONLINE' ? '' : ''}}</li>
                </ul>
              </td>
              <td :rowspan="order.orderDetailList.length" width="8%" class="center">
                <a href="#" class="btn">{{order.processStatus==='UNPAID' ? '' : ''}}</a>
              </td> 
              <td :rowspan="order.orderDetailList.length" width="13%" class="center">
                <ul class="unstyled">
                  <li>
                    <a href="javascript:">|</a>
                  </li>

                </ul>
              </td>
            </template>
          </tr>
        </tbody>
      </table>
    </div>
    <Pagination 
      :currentPage="page"
      :pageSize="limit"
      :total="total"
      :showPageNo="5"
      @currentChange="getOrders"
    />

    <el-pagination
      background
      layout="prev, pager, next, ->, total"
      prev-text=""
      next-text=""
      :total="total"
      :page-size="limit"
      :current-page="page"
      :pager-count="9"
      @current-change="getOrders"
    >
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: 'MyOrder',

    data () {
      return {
        orders: [], // 
        total: 0, // 
        page: 1, //     currentPage / pageNo
        limit: 5, //     pageSize
      }
    },

    async mounted () {
      // 
      this.getOrders()
    },

    methods: {
      /* 
      
      */
      async getOrders (page=1) {
        // page
        this.page = page

        // 
        const result = await this.$API.reqOrders(page, this.limit)
        // , orderstotal, 
        if (result.code===200) {
          const {records, total} = result.data
          this.orders = records
          this.total = total
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  //
  .order-content {
    margin: 0 20px;
    color: #666;

    //
    .title {
      margin-bottom: 22px;
      border: 1px solid #ddd;

      h3 {
        padding: 12px 10px;
        font-size: 15px;
        background-color: #f1f1f1;

      }
    }

    //
    .chosetype {
      margin-bottom: 15px;
      color: #666;

      table {
        border: 1px solid #e6e6e6;
        border-collapse: separate;
        border-radius: 2px;
        width: 100%;
        max-width: 100%;
        border-spacing: 0;

        th {
          padding: 6px 8px;
          color: #666;
          font-weight: 700;
          vertical-align: bottom;
          background-color: #f4f4f4;
          line-height: 18px;
          border-bottom: 1px solid #e6e6e6;
          font-size: 12px;
          text-align: left;
        }
      }
    }

    // 
    .orders {
      font-size: 12px;

      a {
        &:hover {
          color: #4cb9fc;
        }
      }

      table {
        border: 1px solid #e6e6e6;
        border-collapse: collapse;
        border-radius: 2px;
        width: 100%;
        margin-bottom: 18px;
        max-width: 100%;

        th {
          padding: 6px 8px;
          line-height: 18px;
          text-align: left;
          vertical-align: bottom;
          background-color: #f4f4f4;
          font-size: 12px;
          color: #666;

          .pull-right {
            float: right;
            cursor: pointer;

            img {
              margin-right: 10px;
              vertical-align: middle;
            }
          }
        }

        td {
          font-size: 12px;
          color: #666;
          padding: 6px 8px;
          line-height: 18px;
          text-align: left;
          vertical-align: middle;
          border: 1px solid #e6e6e6;

          &.center {
            text-align: center;
          }

          .typographic {
            img {
              float: left;
              margin-right: 10px;
            }

            a {
              float: left;
              min-width: 80px;
              max-width: 250px;
              color: #e1251b;

              &.service {
                color: #666;
              }
            }

            span {
              float: left;
              min-width: 80px;
              max-width: 250px;
              text-align: center;
            }

          }
        }

      }
    }

    // 
    .choose-order {
      .pagination {
        margin: 38px 0;

        ul {
          font-size: 0;
          display: inline-block;

          li {
            display: inline-block;
            padding: 4px 9px;
            font-size: 14px;
            border: 1px solid #e0e9ee;

            &.prev,
            &.next {
              background-color: #fafafa;
            }

            &.prev {
              border-right-color: #28a3ef;
            }

            &.page {
              border-color: #28a3ef;
              border-left: 0;

              &.actived {
                background-color: #28a3ef;

                a {
                  color: #fff;
                }

              }
            }
          }
        }

        div {
          display: inline-block;
          font-size: 14px;
          color: #333;
        }
      }
    }
  }
</style>