<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        <span class="go"> <router-link to="/login"></router-link>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="mobile"
          v-validate="{required: true,regex: /^1\d{10}$/}" name="phone"
          :class="{invalid: errors.has('phone')}">
        <span class="error-msg">{{ errors.first('phone') }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="code"
          name="code" v-validate="{required: true, regex: /^\d{4}$/}"
          :class="{invalid: errors.has('code')}">
        <img ref="code" src="/api/user/passport/code" alt="code" @click="updateCode">
        <span class="error-msg">{{ errors.first('code') }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="password"
           name="" v-validate="{required: true, min: 6, max: 10}"
          :class="{invalid: errors.has('')}">
        <span class="error-msg">{{ errors.first('') }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="password2"
          name="" v-validate="{required: true, is: password}"
          :class="{invalid: errors.has('')}">
        <span class="error-msg">{{ errors.first('') }}</span>
      </div>
      <div class="controls">
        <input type="checkbox" v-model="isAgree"
          name="" v-validate="{agree: true}">
        <span></span>
        <span class="error-msg">{{ errors.first('') }}</span>
      </div>
      <div class="btn">
        <button @click="register"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">6</div>
      <div class="beian">ICP19006430
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Register',

    data () {
      return {
        mobile: '', // 
        code: '', // 
        password: '', // 
        password2: '', // 
        isAgree: false, // 
      }
    },

    methods: {
      /* 
       ==> 
      */
      updateCode () {
        // imgsrc  (, )
        // this.$refs.code.src = '/api/user/passport/code?time=' + Date.now()
        this.$refs.code.src = '/api/user/passport/code'
      },

      /* 
      
      */
      async register () {
        // , 
        const success = await this.$validator.validateAll() // 
        // successtrue, , 
        if (success) {
          // 
          const {mobile, code, password} = this
          try {
            // action
            await this.$store.dispatch('register', {mobile, code, password})
            // , 
            this.$router.replace('/login')
          } catch (error) {
            // , 
            this.updateCode()
            this.code = ''  // 
            alert(error.message)
          }
        } else { // else
          console.log('')
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .register-container {
    .register {
      width: 1200px;
      height: 445px;
      border: 1px solid rgb(223, 223, 223);
      margin: 0 auto;

      h3 {
        background: #ececec;
        margin: 0;
        padding: 6px 15px;
        color: #333;
        border-bottom: 1px solid #dfdfdf;
        font-size: 20.04px;
        line-height: 30.06px;

        span {
          font-size: 14px;
          float: right;

          a {
            color: #e1251b;
          }
        }
      }

      div:nth-of-type(1) {
        margin-top: 40px;
      }

      .content {
        padding-left: 390px;
        margin-bottom: 18px;
        position: relative;

        label {
          font-size: 14px;
          width: 96px;
          text-align: right;
          display: inline-block;
        }

        input {
          width: 270px;
          height: 38px;
          padding-left: 8px;
          box-sizing: border-box;
          margin-left: 5px;
          outline: none;
          border: 1px solid #999;
          &.invalid {
            border: solid 1px red;
          }
        }

        img {
          vertical-align: sub;
        }

        .error-msg {
          position: absolute;
          top: 100%;
          left: 495px;
          color: red;
        }
      }

      .controls {
        text-align: center;
        position: relative;

        input {
          vertical-align: middle;
        }

        .error-msg {
          position: absolute;
          top: 100%;
          left: 495px;
          color: red;
        }
      }

      .btn {
        text-align: center;
        line-height: 36px;
        margin: 17px 0 0 55px;

        button {
          outline: none;
          width: 270px;
          height: 36px;
          background: #e1251b;
          color: #fff !important;
          display: inline-block;
          font-size: 16px;
        }
      }
    }

    .copyright {
      width: 1200px;
      margin: 0 auto;
      text-align: center;
      line-height: 24px;

      ul {
        li {
          display: inline-block;
          border-right: 1px solid #e4e4e4;
          padding: 0 20px;
          margin: 15px 0;
        }
      }
    }
  }
</style>